import React from "react"
import { Route, Switch } from "react-router"
import Layout from "../components/Layout"
import Contact from "../pages/Contact"
import Diagram from "../pages/Diagram"
import Faq from "../pages/Faq"
import Help from "../pages/Help"
import Landing from "../pages/Landing"
import Videos from "../pages/Videos"

const RoutesList = [
  {
    path: "/",
    component: Landing,
    exact: true
  },
  {
    path: "/lekh-diagram",
    component: Diagram,
    exact: true
  },
  {
    path: "/videos",
    component: Videos,
    exact: true
  },
  {
    path: "/help",
    component: Help,
    exact: true
  },
  {
    path: "/faq",
    component: Faq,
    exact: true
  },
  {
    path: "/contact",
    component: Contact,
    exact: true
  }
]

const Routes = () => {
  return (
    <Switch>
      <Layout>
        {RoutesList.map(r => (
          <Route
            path={r.path}
            exact={r.exact}
            component={r.component}
            key={r.path}
          />
        ))}
      </Layout>
    </Switch>
  )
}

export default Routes
