import React, { useRef, useState } from "react"
// import useClickOutside from "../../hooks/useClickOutside"
import "../../stylesheets/_faq.scss"
import { AiOutlineCaretDown, AiOutlineCaretRight } from "react-icons/ai"

const FaqsModal = ({ answer, question }) => {
  const ref = useRef()
  const [showAnswer, setShowAnswer] = useState(false)
  // useClickOutside(ref, () => setShowAnswer(false))

  return (
    <div className="faqs-question" ref={ref}>
      <div className="question-div" onClick={() => setShowAnswer(!showAnswer)}>
        <span className="caret">
          {!showAnswer && <AiOutlineCaretRight />}
          {showAnswer && <AiOutlineCaretDown />}
        </span>

        <h4 className="question">{question}</h4>
      </div>

      {showAnswer && <p className="answer">{answer}</p>}
    </div>
  )
}

export default FaqsModal
